.main-epfo-header{
    background: #FFF;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content:end;
    width: 100%;
    box-shadow: 18px -6px 6px 6px rgb(163, 163, 163);
}
.user-info {
  margin-right: 8px;
  text-align: right;
}

.name {
  font-size: 14px;
  font-weight: 500;
  color: #2c2c2c;
}

.role {
  font-size: 12px;
  color: #9e9e9e;
}

.avatar {
  width: 40px;
  height: 40px;
  background-color: rgba(126, 87, 194, 0.1); /* light purple */
  color: #673ab7; /* purple text */
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-left: 8px;
  overflow: hidden; /* ensures image stays within circle */
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;   /* crops & centers image */
  object-position: top;
  display: block;      /* removes inline gap below img */
  border-radius: 50%;  /* circular crop */
}


.btn-check:checked+.btn, .btn.active, .btn.show, .btn:first-child:active, :not(.btn-check)+.btn:active {
    color: var(--bs-btn-active-color);
    background-color: var(--bs-btn-active-bg);
    border-color:#ffffff00;
}
